.donate-page {
  position: absolute;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  padding: 10em;
  top: 10%;
}

.row {
  flex-direction: row;
  margin-bottom: 60vh;
}

.link-container {
  flex: 40%;
}

.donate-info-container {
    background: white;
    color: #3c3b6e;
    height: auto;
    border: 3px solid #3c3b6e;
    flex: 60%;
    font-size: 3vh;
  }

  .link-btn-container-2 {
    border: 2px solid white;
    height: 50px;
    width: 200px;
    margin: auto;
    margin-top: 15em;
  }

h3 {
  margin: 1em;
  font-size: 4vh;
}

.address {
  width: 20vw;
  height: auto;
  border: 2px solid #3c3b6e;
  margin: auto;
}

  @media only screen and (max-width: 700px) {
    h3 {
        font-size: 18px;
    }

    .donate-page {
        font-size: 12px;
        display: flex;
        flex-direction: column;
        width: 100%;
        padding: 0;
        padding-top: 10em;
    }

    .row {
      align-self: center;
      width: 100%;
      margin-bottom: 2em;
    }

    .donate-info-container {
        background: white;
        color: #3c3b6e;
        width: 100%;
        height: auto;
        border: 3px solid #3c3b6e;
        flex: 100%;
        align-self: center;
      }

    .link-container {
      flex: 100%;
      align-self: center;
    }

    .link-btn-container-2 {
      background: #3c3b6e;
      border: 2px solid white;
      height: 50px;
      width: 200px;
      align-self: center;
      margin-bottom: 5em;
      margin-top: 0;
    }

    .address {
      width: 80%;
    }

  }