.store-items-container {
    display: flex;
    margin: auto;
    width: 80%;
}

.row {
    display: flex;
    flex-wrap: wrap;
    padding: 0 4px;
}

.column {
    flex: 25%;
    max-width: 25%;
    padding: 0 4px;
}

.store-item {
   width: 8em;
   height: auto; 
   vertical-align: middle;
   margin: 2em;
}

.store-item:hover {
    opacity: 0.8;
    cursor: pointer;
}

.store-item-img {
    width: 100%;
    height: auto;
}

.shop-h1 {
    color: #3c3b6e;
    font-family: Gotham;
    font-size: 72px;
    margin: 1em;
    float: left;
}